.chats {
  position: relative;
  z-index: 1000;
  height: 100%;
  border-right: 1px solid var(--light-border);
  background: var(--clr-8);
}
.chats__title {
  flex: 1;
  display: flex;
  justify-content: space-around;
  margin: 0 1rem;
  border-bottom: 1px solid var(--light-border);
  padding: 1rem 0;
}
.chats__title__search {
  flex: 15;
  border: 0;
  background: var(--light-background);
  color: var(--black);
  padding: 0.25rem 1rem;
  border-radius: 8px;
  outline: none;
}
.chats__title__search::placeholder {
  color: var(--grey);
}
.chats__list {
  height: 100%;
  overflow: hidden;
  overflow-y: scroll;
  width: inherit;
}
.chats__list__creating-chat {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 16px;
}
